.search-bar {
  margin-bottom: 5px;
  display: flex;
  align-content: center;
}

.location-submit {
  background: transparent;
  border: 2px solid transparent;
  box-sizing: border-box;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 700;
  line-height: 0;
  margin: 18px;
  padding: 15px 25px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  outline: none;
  position: relative;
  top: 0;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.5);
  -webkit-transition: all 0.2s ease-in-out 0s;
  -moz-transition: all 0.2s ease-in-out 0s;
  -ms-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
  background: #3cbce1;
  color: #fff;
  box-shadow: 0 3px 0 #1fa4cb;
}

.location-submit:hover {
  background: #56d7ed;
}

.location-submit:active,
.location-submit:focus {
  background: #2fb7df;
}

.search-form {
  padding-top: 25px;
}

@media all and (max-width: 500px) {
  .search-form {
    align-self: center;
    position: relative;
  }
  .search-bar {
    flex-direction: column;
  }
  .location-submit {
    height: 50%;
    width: 50%;
    align-self: center;
  }
}
